.navbar {
  font-size: 16px;
  display: flex;
  list-style-type: none;
}

.navbar li:nth-child(2) {
  text-decoration: underline;
}

.navbar li {
  cursor: pointer;
}

.navbar a {
  text-decoration: none;
  color: inherit;
}

.navbar a:hover {
  text-decoration: none;
  color: inherit;
}
